// 
.hide-text {
    text-indent: 1100%;
    white-space: nowrap;
    overflow: hidden;
}

// 
@mixin clearfix() {
    &::before,
    &::after{
        content: "";
        display: table;
    }
    &::after{
        clear: both;
    }
}

.clearfix {
    &::before,
    &::after{
        content: "";
        display: table;
    }
    &::after{
        clear: both;
    }
}

// 
.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}